
body {
    margin: 0
}

.dx-list-item-content {
    padding-left: 10px !important;
}

.dx-button {
    background-color: rgba(191, 191, 191, -0.15);
    border: none;
}

.dx-scrollable-content {
    background-color: #455671;
}

.dx-toolbar-items-container {
    background-color: #455671;
    color: #fff;
}

.dx-button-content i {
    color: white !important;
    font-size: 3em;
}

.dx-button-has-icon .dx-icon {
    font-size: 2em;
}

.search-bar {
    border: 1px solid white;
    border-radius: 5px;
}

.search-bar i {
    left: 10px;
    top: 5px;
    color: grey;
}

.search-bar input {
    background-color: transparent;
}

.dx-drawer-panel-content {
    width: 350px;
    /*#455671*/
}

.dx-toolbar .dx-toolbar-after {
    padding-right: 15px;
}

.dx-toolbar-after .dx-toolbar-item:first-child
.dx-drawer-expand.dx-drawer-right .panel-list {
    float: right;
}

.dx-state-hover {
    color: white !important;
    background-color: rgba(157, 172, 195, 0.5) !important;
}

.dx-state-focused {
    color: white !important;
    background-color: rgba(157, 172, 195, 0.5) !important;
}

.panel-list .dx-list-item {
    color: #fff;
    border-top: 1px solid rgba(221, 221, 221, .2);
}

.panel-list .dx-list-item .dx-icon {
    color: #fff !important;
}

.options {
    padding: 20px;
    background-color: rgba(191, 191, 191, .15);
}

.caption {
    font-size: 18px;
    font-weight: 500;
}

.option {
    margin-top: 10px;
    display: inline-block;
    margin-right: 50px;
}

label {
    font-weight: bold;
}

#content {
    height: 100%;
    padding: 10px 20px;
}

#content h2 {
    font-size: 26px;
}


.dx-drawer {
    width: 39px;
}

.dx-drawer-opened {
    width: 350px !important;
}

.opened_toolbar .dx-toolbar-items-container {
    width: 350px;
    display: flex;
    justify-content: space-between;
    height: 60px !important;
}

.search-bar {
    background-color: #455671;
    padding: 3px;
    margin: 0 3px;
}

.dx-drawer-panel-content {
    background-color: #455671;
}

.closed_toolbar .dx-toolbar-items-container .dx-drawer-panel-content {
    width: 39px;
}

.burger {
    display: none !important;
}

.closed_toolbar {
    position: relative;
}

.closed_toolbar:hover .burger {
    display: block !important;
    position: absolute;
    top: 5px;
    left: 39px;
}

.dx-item-content span {
    color: white;
}

.dx-treeview-node-container li {
    margin-left: 0;
}

.dx-treeview-toggle-item-visibility {
    right: 4px;
    left: auto;
}

li[aria-level="1"] {
    padding-left: 0;
}

li[aria-level="2"] {
    padding-left: 20px;
}

.dx-item-content i {
    margin-left: 5px;
    margin-right: 10px !important;
    color: #fff;
}

.profile_container {
    position: absolute;
    text-align: center;
    width: 240px;
    top: 30px;
    right: -230px;
    transition: right .5s ease;
    color: grey;
}

.profile_container i{
    font-size: 2em;
}

.profile_container:hover {
    position: absolute;
    right: 0;
    transition: right .5s ease;
}
.profile_container_fixed{
    position: absolute;
    text-align: center;
    width: 240px;
    top: 30px;
    right: 0;
    transition: right .5s ease;
    color: grey;
}